import React from "react";
import "./styles.css";
import { useState } from "react";

const bookDB = {
  mass: [
    { name: "The Great Father", rating: "4.5/5" },
    { name: "Lucifer", rating: "4/5" }
  ],

  comedy: [
    {
      name: "Aadu Ori Beekara Jeeviyanu",
      rating: "4/5"
    },
    {
      name: "Vellimoonga",
      rating: "4/5"
    }
  ],
  feelgood: [
    {
      name: "Kumbalangi Nights",
      rating: "5/5"
    },
    {
      name: "Premam",
      rating: "5/5"
    }
  ]
};

export default function App() {
  const [selectedGenre, setGenre] = useState("mass");
  function genreClickHandler(genre) {
    setGenre(genre);
  }
  return (
    <div className="App">
      <h1> ðŸŽ¬ movie collections </h1>
      <p style={{ fontSize: "smaller" }}>
        Checkout my favorite movies. Select a genre to get started{" "}
      </p>

      <div>
        {Object.keys(bookDB).map((genre) => (
          <button
            onClick={() => genreClickHandler(genre)}
            style={{
              cursor: "pointer",
              background: "yellowgreen",
              borderRadius: "0.5rem",
              padding: "0.5rem  1rem",
              border: "1px solid green",
              margin: "1rem 0.3rem"
            }}
          >
            {genre}
          </button>
        ))}
      </div>
      <hr />
      <div style={{ textAlign: "left" }}>
        <ul style={{ paddingInlineStart: "0" }}>
          {bookDB[selectedGenre].map((book) => (
            <li
              key={book.name}
              className="card"
              style={{
                listStyle: "none",
                padding: "1rem",
                border: "1px solid yellowgreen",
                width: "70%",
                margin: "1rem 0rem",
                borderRadius: "0.5rem"
              }}
            >
              <div style={{ fontSize: "larger" }}> {book.name} </div>
              <div style={{ fontSize: "smaller" }}> {book.rating} </div>
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
}
